import axios from 'axios';
import CONFIG from '@/config';
import UserService from '@/services/login';
import { Message } from 'element-ui';
import storage from '@/services/localStorage';

axios.defaults.baseURL = CONFIG.baseUrl;
axios.defaults.headers.common['Content-Language'] = storage.lang || '';

// 全局拦截器 - Request
axios.interceptors.request.use(config => config, (error) => {
  return Promise.reject(error.request);
});

// 全局拦截器 - Response
axios.interceptors.response.use(response => {
  const status = response && response.data.status;
  const msg = response && response.data.msg;
  if (status !== '200') {
    if (msg) {
      Message.warning({
        message: msg,
      });
    }
    return Promise.reject(response);
  } else {
    return Promise.resolve(response);
  }
}, error => {
  const code = error.response.status;
  if (code === 401) {
    UserService.logout();
    Message.error({
      message: '登录令牌已失效，请重新登录！',
      center: true,
    });
    return;
  }
  const msg = error.response.data.msg || error.response.data.message;
  if (msg) {
    Message.error({
      message: msg,
    });
  }
  return Promise.reject(error.response);
});

export default axios;



