import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/login.vue';
import Main from '@/components/main.vue';
import Dashboard from '@/components/dashboard/index';
import StaticContent from '@/components/static/index.vue';
import StaticContentEdit from '@/components/static/edit.vue';
import News from '@/components/news/news.vue';
import NewsCreate from '@/components/news/create.vue';
import NewsEdit from '@/components/news/edit.vue';
import Links from '@/components/links/index.vue';
import Hospital from '@/components/hospitals/index.vue';
import HospitalCreate from '@/components/hospitals/create.vue';
import HospitalEdit from '@/components/hospitals/edit.vue';
import Customization from '@/components/customization/index.vue';
import storage from '@/services/localStorage';
import UserService from '@/services/login';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main,
      redirect: '/dashboard',
      children: [
        {
          path: '/dashboard',
          name: 'dashboard',
          component: Dashboard,
        },
        {
          path: 'static',
          name: 'static',
          component: StaticContent,
        },
        {
          path: 'static/edit/:id',
          name: 'static-edit',
          component: StaticContentEdit,
        },
        // 新闻中心管理
        {
          path: 'news',
          name: 'news',
          component: News,
        },
        {
          path: 'news/create',
          name: 'news-create',
          component: NewsCreate,
        },
        {
          path: 'news/edit/:id',
          name: 'news-edit',
          component: NewsEdit,
        },
        {
          path: 'links',
          name: 'links',
          component: Links,
        },
        {
          path: 'hospital',
          name: 'hospital',
          component: Hospital,
        },
        {
          path: 'hospital/create',
          name: 'hospital-create',
          component: HospitalCreate,
        },
        {
          path: 'hospital/edit/:id',
          name: 'hospital-edit',
          component: HospitalEdit,
        },
        // 静态资源定制化
        {
          path: 'customization',
          name: 'customization',
          component: Customization,
        },
      ],
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      path: '*',
      direct: '/news',
    },
  ],
});
// 路由白名单
const whitelist = [];

// 路由检测服务
router.beforeEach((to, from, next) => {
  // if (whitelist.includes(to.path)) return next();
  // Token 缓存检测
  if (!storage.token) {
    storage.clear();
    if (to.path !== '/login') return next('/login');
    return next();
  } else {
    UserService.initToken(storage.token);
    if (to.path === '/login') {
      // 存在token时，禁止login路由
      return next(from.path);
    }
    return next();
  }
});

export default router;



