<template>
  <div class="setting-section">
    <div class="setting-title">
      <span>新闻中心</span>
    </div>
    <div class="setting-content" v-loading="loading">
      <div class="hd-control">
        <div class="flexbox-between mb-md" v-if="!showCheckbox">
          <div>
            <el-button
              type="primary"
              @click="create">发布新闻</el-button>
            <el-button
              type="text"
              class="warning"
              icon="el-icon-delete"
              @click="toggleCheckbox">批量删除</el-button>
          </div>
          <div>
            <span class="small-text">新闻类型：</span><br>
            <el-radio-group v-model="status" size="mini">
              <el-radio-button label="">
                <span>全部新闻</span>
              </el-radio-button>
              <el-radio-button label="中心动态">
                <span>中心动态</span>
              </el-radio-button>
              <el-radio-button label="通知公告">
                <span>通知公告</span>
              </el-radio-button>
              <el-radio-button label="行业资讯">
                <span>行业资讯</span>
              </el-radio-button>
              <el-radio-button label="图片新闻">
                <span>图片新闻</span>
              </el-radio-button>
              <el-radio-button label="技术专题">
                <span>技术专题</span>
              </el-radio-button>
            </el-radio-group>
          </div>
        </div>
        <div class="flexbox-around" v-else>
          <el-button
            type="danger"
            @click="batRemove">删除</el-button>
          <el-button
            type="info"
            class="ml-lg"
            @click="toggleCheckbox">取消</el-button>
        </div>
      </div>
      <el-table
        :data="newsList"
        style="width: 100%"
        @row-click="setChecked">
        <template v-if="showCheckbox">
          <el-table-column width="30">
            <template slot-scope="scope">
              <el-checkbox v-model="checked" :label="scope.row.id"></el-checkbox>
            </template>
          </el-table-column>
        </template>
        <el-table-column
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          label="标题">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.hot" type="warning">HOT</el-tag>
            <span
              class="primary"
              type="text"
              v-if="!showCheckbox"
              @click="view(scope.row.type, scope.row.id)">{{ scope.row.title }}</span>
            <span
              type="text"
              v-else>{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          label="新闻类型"
          width="80">
        </el-table-column>
        <el-table-column
          prop="update_date"
          label="最后修改时间"
          sortable
          width="150">
        </el-table-column>
        <el-table-column
          prop="page_view"
          label="浏览量"
          sortable
          width="80">
        </el-table-column>
        <el-table-column label="首页轮播" width="80" v-if="status === '图片新闻'">
          <template slot-scope="scope">
            <el-button
              type="text"
              v-if="scope.row.url"
              style="color: #e6a23c;"
              @click="setBanner(scope.row.id)">更新</el-button>
            <el-button
              type="text"
              v-else
              @click="setBanner(scope.row.id)">开启</el-button>
            <el-button
              type="text"
              v-if="scope.row.url"
              style="color: #909399;"
              @click="removeBanner(scope.row.title, scope.row.id)">关闭</el-button>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="270">
          <template slot-scope="scope" v-if="!showCheckbox">
            <el-button
              v-if="!scope.row.hot && scope.row.type !== '技术专题'"
              size="mini"
              type="primary"
              @click="setHots(scope.row.id)">HOT</el-button>
            <el-dropdown
              v-if="['admin', 'zdyfy'].includes(username)"
              split-button
              type="primary"
              size="mini"
              @click="edit(scope.row.id)">编辑
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="openEditTimeDialog(scope.row.id)">修改时间</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button
              size="mini"
              v-else
              @click="edit(scope.row.id)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="remove(scope.row.title, [scope.row.id])">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-if="!showCheckbox"
        class="mt-sm"
        layout="total, sizes, prev, pager, next, jumper"
        :total.sync="count"
        :page-size.sync="pageSize"
        :page-sizes="[10, 15, 20, 30, 50]"
        :current-page.sync="pageNumber">
      </el-pagination>
      <el-dialog
        title="修改新闻时间"
        :visible.sync="editTimeDialogVisible"
        width="400px">
        <el-form label-width="100px" :model="newsTime">
          <el-form-item label="发布时间">
            <el-date-picker
              v-model="newsTime.publishDate"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择发布时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="更新时间">
            <el-date-picker
              v-model="newsTime.updateDate"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择更新时间">
            </el-date-picker>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editTimeDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editTime">立即修改</el-button>
        </span>
      </el-dialog>
      <el-dialog
        title="上传轮播图片（Banner）"
        :visible.sync="dialogVisible"
        width="480px">
        <el-upload
          drag
          ref="upload"
          action="string"
          :auto-upload="false"
          :http-request="uploadFunc"
          class="upload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">
            <span style="color: red; line-height: 1.5;">* 仅图片 (比例 2:1，且分辨率不小于 1920*960), 大小不超过 10M！</span>
            <span style="color: red; line-height: 1.5;">* 上传封面后，首页将自动轮播此新闻 *</span>
          </div>
        </el-upload>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="upload">上 传</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import NewsServices from '@/services/news';
import UserServices from '@/services/login';

export default {
  name: 'news',
  data() {
    return {
      newsList: [],
      pageNumber: 1,
      count: 0,
      showCheckbox: false,
      pageSize: 15,
      checked: [],
      status: '',
      loading: false,
      dialogVisible: false,
      editTimeDialogVisible: false,
      newsId: '',
      newsTime: {
        publishDate: '',
        updateDate: '',
      },
      id: '',
      username: '',
    };
  },
  watch: {
    pageSize(val) {
      this.getNewsData();
    },
    pageNumber(val) {
      this.getNewsData();
    },
    status() {
      this.getNewsData();
    },
  },
  methods: {
    getType(type) {
      switch (type) {
        case '中心动态':
          return 'news';
        case '通知公告':
          return 'notice';
        case '行业资讯':
          return 'infos';
        case '图片新闻':
          return 'pic-news';
        case '技术专题':
          return 'subject';
        default:
          return '';
      };
    },
    getNewsData() {
      this.loading = true;
      const type = this.status;
      const pageNum = this.pageNumber;
      const pageSize = this.pageSize;

      NewsServices.getList(pageSize, pageNum, type)
        .then((res) => {
          const data = res.data.data;
          this.newsList = data.content;
          this.count = data.totalElements;
          this.loading = false;
        }).catch(() => {
          this.loading = false;
        });
    },
    toggleCheckbox() {
      this.showCheckbox = !this.showCheckbox;
      this.checked = [];
    },
    setChecked(row) {
      if (!this.showCheckbox) return;
      const id = row.id;
      const arr = this.checked;
      const index = arr.indexOf(row.id);
      if (index < 0) {
        this.checked.push(id);
      } else {
        this.checked.splice(index, 1);
      }
    },
    // 批量删除
    batRemove() {
      const arr = this.checked;
      this.remove(`共${arr.length}条`, arr);
    },
    // 发布新闻
    create() {
      this.$router.push({ name: 'news-create' });
    },
    // 设为热点新闻
    setHots(id) {
      this.$confirm('设为热点新闻', '提示', {
        type: 'warning',
      }).then(() => {
        NewsServices.setHots(id)
          .then((res) => {
            this.$message.success('热点新闻设置成功！');
            this.getNewsData();
          });
      });
    },
    // 查看新闻
    view(type, id) {
      const baseUrl = 'http://htcc.org.cn';
      if (type === '技术专题') {
        window.open(`${baseUrl}/page/research/${this.getType(type)}-article/?id=${id}`, '_blank');
      } else {
        window.open(`${baseUrl}/page/news/${this.getType(type)}-article/?id=${id}`, '_blank');
      }
    },
    // 编辑新闻
    edit(id) {
      this.$router.push({ name: 'news-edit', params: { id } });
    },
    openEditTimeDialog(id) {
      this.newsId = id;
      NewsServices.getContent(this.newsId)
        .then((res) => {
          const data = res.data.data;
          this.newsTime.publishDate = data.publish_date;
          this.newsTime.updateDate = data.update_date;
          this.editTimeDialogVisible = true;
        });
    },
    editTime() {
      const id = this.newsId;
      const publishDate = this.newsTime.publishDate;
      const updateDate = this.newsTime.updateDate;

      NewsServices.editTime(id, publishDate, updateDate)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success('时间修改成功！');
            this.editTimeDialogVisible = false;
          }
        }).catch(() => {
          this.editTimeDialogVisible = false;
        });
    },
    // 删除新闻
    remove(title, id) {
      this.$confirm(`删除 "<strong>${title.substring(0, 15)}..."</strong> 新闻`, '提示：删除后不可恢复！', {
        type: 'error',
        dangerouslyUseHTMLString: true,
      }).then(() => {
        NewsServices.deleteNews(id)
          .then(() => {
            this.$message.success('删除成功！');
            this.getNewsData();
          });
      });
    },
    setBanner(id) {
      if (id) {
        this.id = id;
        this.dialogVisible = true;
      }
    },
    removeBanner(title, id) {
      if (id) {
        this.$confirm(`关闭 "<strong>${title.substring(0, 15)}..."</strong> 首页轮播`, '提示：关闭后不可恢复，需重新上传', {
          type: 'warning',
          dangerouslyUseHTMLString: true,
        }).then(() => {
          NewsServices.removeBanner(id)
            .then(() => {
              this.$message.success('已关闭此新闻首页轮播！');
              this.getNewsData();
            });
        });
      }
    },
    uploadFunc(data) {
      const isLt10M = data.file.size / 1024 / 1024 < 10;
      if (!isLt10M) {
        this.$message.error('文件大小超过限制(10M)!');
        this.$refs.upload.clearFiles();
        return;
      }
      const file = data.file;
      const id = this.id;
      const params = new FormData();
      params.append('id', id);
      params.append('image', file);

      NewsServices.importImage(params)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success(res.data.msg);
            this.dialogVisible = false;
          } else {
            this.$message.warning(res.data.msg);
            this.$refs.upload.clearFiles();
          }
        });
    },
    upload() {
      this.$refs.upload.submit();
    },
  },
  created() {
    UserServices.getUser()
      .then((res) => {
        this.username = res.data.data.username;
      });
    this.getNewsData();
  },
};
</script>
<style lang="scss" scoped>
.el-pagination {
  text-align: center;
}
.el-button+.el-button {
  margin-left: 2px;
}
.flexbox-around {
  display: flex;
  justify-content: center;
}
.small-text {
  color: #999;
  padding-left: 15px;
  line-height: 2;
}
.upload {
  margin: 0 auto;
  width: 360px;
}
.primary {
  cursor: pointer;
}
</style>



