<template>
  <div class="setting-section" v-loading="loading">
    <div class="setting-title">
      <div class="flexbox-between">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ name: 'static' }">静态内容</el-breadcrumb-item>
          <el-breadcrumb-item>编辑内容</el-breadcrumb-item>
          <el-breadcrumb-item>{{ news.title }}</el-breadcrumb-item>
        </el-breadcrumb>
        <back-btn>返回</back-btn>
      </div>
    </div>
    <div class="setting-content">
      <el-form ref="news" :model="news" label-width="100px" label-position="left">
        <el-form-item label="标题">
          <el-input v-model="news.title" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="摘要">
          <el-input type="textarea" v-model="news.tabloid" size="mini" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="附件">
          <el-upload
            multiple
            class="upload"
            ref="upload"
            :action="actions"
            :headers="headers"
            :limit="10"
            :on-change="handleChange"
            :on-remove="handleRemove"
            :file-list="fileList">
            <el-button size="mini" type="primary" v-if="!fileList.length">添加附件</el-button>
            <el-button size="mini" type="primary" v-else>继续添加附件</el-button>
            <span slot="tip" class="small-text ml-md" style="color: red;">* 支持多文件上传！(最多 10个, 最大 100M!)</span>
          </el-upload>
        </el-form-item>
        <el-form-item label="内容">
          <div style="height: 500px;">
            <quill-editor
              v-model="news.content"
              ref="quillEditor"
              style="height: 85%;"
              :config="editorOption">
            </quill-editor>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            @click="editNews">立即更新</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import CONFIG from '@/config';
import axios from 'axios';
import BackBtn from '@/components/common/back';
import NewsServices from '@/services/news';
import { replaceImgSrc, formatImgSrc, setImgPrefix } from '@/services/utils';

export default {
  name: 'static-edit',
  data() {
    return {
      news: {
        title: '',
        tabloid: '',
        content: '',
        attachments: '',
      },
      fileList: [],
      actions: `${CONFIG.baseUrl}attachment/asyncUploadFile`,
      editorOption: {},
      loading: false,
    };
  },
  computed: {
    headers() {
      return { Authorization: axios.defaults.headers.common.Authorization };
    },
  },
  methods: {
    getNews(id) {
      NewsServices.getContent(id)
        .then((res) => {
          const data = res.data.data;
          data.content = setImgPrefix(data.content);
          this.news = data;
          this.fileList = this.news.attachments;
        });
    },
    editNews() {
      this.loading = true;
      const news = this.news;
      const id = news.id;
      const title = news.title;
      const tabloid = news.tabloid;
      const _content = news.content;
      const attachments = this.getFileList(this.fileList);
      if (!title) {
        this.$message.warning('请填写标题！(1 - 50字)');
        return;
      }
      if (!_content) {
        this.$message.warning('请输入内容！内容不可为空！');
        return;
      }
      formatImgSrc(_content).then(axios.spread((...args) => {
        const arr = [];
        args.map((val) => {
          arr.push(val.data.data.url);
        });
        const content = `<div class="ql-editor">${replaceImgSrc(_content, arr)}</div>`;
        
        NewsServices.edit(id, title, '静态', tabloid, content, attachments)
          .then((res) => {
            this.loading = false;
            this.$message.success('更新成功！');
            this.$router.back();
          }).catch(() => {
            this.loading = false;
          });
      })).catch(() => {
        this.loading = false;
      });
    },
    // 控制文件列表
    handleChange(file, fileList) {
      this.fileList = fileList.slice(-10);
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    getFileList(files) {
      const list = [];
      files.map((val) => {
        if (val.response) {
          list.push(val.response.data[0]);
        } else {
          list.push(val);
        }
      });
      return list;
    },
  },
  created() {
    const id = parseInt(this.$route.params.id, 10);
    this.getNews(id);
  },
  components: { BackBtn },
};
</script>
<style lang="scss" scoped>
.el-form {
  .el-form-item {
    margin-top: 20px;
  }
}
</style>



