// 缓存
const PREFIX = 'ntc_';

const formatKey = key => (PREFIX + key).toUpperCase();

// Token 令牌
const AUTH_TOKEN = formatKey('token');
const LANGUAGE = formatKey('lang');

export default {
  get token() {
    return localStorage.getItem(AUTH_TOKEN);
  },
  set token(t) {
    localStorage.setItem(AUTH_TOKEN, t);
  },
  get lang() {
    return localStorage.getItem(LANGUAGE);
  },
  set lang(l) {
    localStorage.setItem(LANGUAGE, l);
  },
  clearUserinfo: () => {
    // 用户相关 key
    const userKey = [AUTH_TOKEN];
    userKey.forEach((item) => {
      localStorage.removeItem(item);
    });
  },
  remove: (s) => {
    localStorage.removeItem(PREFIX + s);
  },
  clear: () => {
    localStorage.clear();
  },
};



