import $http from '@/services/http';

const getHospitals = (pageNum) => $http.get('/hospital/findAllByPage', {
  params: {
    pageSize: 4,
    pageNum,
  },
  headers: {
    'Content-Language': 'cn',
  },
});

const getTopList = () => $http.get('/hospital/getStickHospital');

const getHospitalById = id => $http.get(`/hospital/getHospitalById?id=${id}`);

const createHospital = (name, nameEn, image, url, addressName, addressNameEn) => $http.post('/hospital/insertHospital', {
  name, nameEn, image, url, addressName, addressNameEn,
});

const updateHospital = (id, name, nameEn, image, url, addressName, addressNameEn,) => $http.post('/hospital/updateHospital', {
  id, name, nameEn, image, url, addressName, addressNameEn,
});

const deleteHospital = id => $http.post('/hospital/deleteHospital', {
  groupId: [id],
});

const moveUp = id => $http.get(`/hospital/setHospitalPriority?id=${id}&priority=rise`);

const moveDown = id => $http.get(`/hospital/setHospitalPriority?id=${id}&priority=drop`);

const setTop = id => $http.get(`/hospital/setStickHospital?id=${id}`);

export default {
  getHospitals,
  getTopList,
  getHospitalById,
  createHospital,
  updateHospital,
  deleteHospital,
  moveUp,
  moveDown,
  setTop,
};



