<template>
  <div class="setting-section">
    <div class="setting-title flexbox-between">
      <span class="pb-md">协同医院</span>
      <div>
        <el-button type="text" @click="openDialog" size="small">查看首页医院</el-button>
        <el-button
          type="text"
          class="p-none pb-md"
          icon="el-icon-plus"
          size="small"
          @click="create">新增</el-button>
      </div>
    </div>
    <div class="setting-content">
      <div v-for="list in lists" :key="list.id">
        <h3 class="title">{{ list.address_name }}</h3>
        <el-row class="pt-md">
          <el-col :xs="12" :sm="12" :md="8" :lg="4" v-for="item in list.hospitals" :key="item.id" style="padding: 0 10px;">
            <el-card :body-style="{ padding: '0px' }" :class="{ 'show': item.stick}">
              <img class="img" :src="baseUrl + item.image">
              <p class="name">{{ item.name }}</p>
              <p class="name-en">{{ item.nameEn || '-' }}</p>
              <p class="small-text cursor" @click="openNewpage(item.url)">{{ item.url }}</p>
              <div class="content">
                <el-button
                  type="text"
                  size="small"
                  class="button p-xs"
                  v-if="!item.stick"
                  @click="showHome(item.id)">首页显示</el-button>
                <el-button
                  type="text"
                  size="small"
                  class="danger button p-xs"
                  v-else
                  @click="removeHome(item.id)">取消显示</el-button>
              </div>
              <div class="content pb-xs">
                <el-button
                  type="text"
                  size="small"
                  class="button p-xs"
                  @click="edit(item.id)">编辑</el-button>
                <el-button
                  type="text"
                  size="small"
                  class="danger button p-xs"
                  @click="deletes(item.id)">删除</el-button>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <el-pagination
        class="mt-sm"
        layout="prev, pager, next"
        :current-page.sync="pageNumber"
        :page-count="count">
      </el-pagination>
    </div>
    <el-dialog
      title="显示在首页的医院"
      :visible.sync="dialogTableVisible"
      width="65%">
      <el-table
        :data="topList"
        style="width: 100%">
        <el-table-column
          label="医院">
          <template slot-scope="scope">
            <span>{{ scope.row.name }}</span>
            <span v-if="scope.row.nameEn" class="small-text"><br>{{ scope.row.nameEn }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="url"
          label="首页地址">
        </el-table-column>
        <el-table-column
          label="所属城市"
          width="180">
          <template slot-scope="scope">
            <span>{{ scope.row.addressName }} </span>
            <span v-if="scope.row.addressNameEn" class="small-text">{{ scope.row.addressNameEn }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="80">
          <template slot-scope="scope">
            <el-button
              class="p-xs"
              @click="moveUp(scope.row.id)"
              v-if="scope.$index > 0">⬆</el-button>
            <el-button
              class="p-xs ml-none"
              @click="moveDown(scope.row.id)"
              v-if="scope.$index < topList.length - 1">⬇</el-button>
          </template>
        </el-table-column>
        <el-table-column label="移除" width="50">
          <template slot-scope="scope">
            <el-button
              class="danger"
              type="text"
              @click="removeHome(scope.row.id, true)">移除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
import hospitalService from '@/services/hospital';
import CONFIG from '@/config';

export default {
  name: 'customization',
  data() {
    return {
      baseUrl: 'http://htcc.org.cn',
      lists: [],
      topList: [],
      count: 0,
      pageNumber: 1,
      dialogTableVisible: false,
    };
  },
  watch: {
    pageNumber() {
      this.getList();
    },
  },
  methods: {
    openNewpage(url) {
      window.open(url, '_blank');
    },
    openDialog() {
      this.getTopList();
      this.dialogTableVisible = true;
    },
    getList() {
      const pageNumber = this.pageNumber;

      hospitalService.getHospitals(pageNumber)
        .then((res) => {
          if (res.data.status === '200') {
            this.lists = res.data.data.content;
            this.count = res.data.data.totalPages;
          }
        });
    },
    getTopList() {
      hospitalService.getTopList()
        .then((res) => {
          if (res.data.status === '200') {
            this.topList = res.data.data;
          }
        });
    },
    moveUp(id) {
      hospitalService.moveUp(id)
        .then((res) => {
          if(res.data.status === '200') {
            this.getTopList();
          }
        });
    },
    moveDown(id) {
      hospitalService.moveDown(id)
        .then((res) => {
          if(res.data.status === '200') {
            this.getTopList();
          }
        });
    },
    showHome(id) {
      this.$confirm('首页显示此医院？', '提示', {
        type: 'warning',
      }).then(() => {
        hospitalService.setTop(id)
          .then((res) => {
            if (res.data.status === '200') {
              this.$message.success('置顶成功！');
            } else {
              this.$message.warning(res.data.msg);
            }
            this.getList();
          });
      });
    },
    removeHome(id, isAnswer) {
      if (!isAnswer) {
        this.$confirm('不在首页显示此医院？', '提示', {
          type: 'warning',
        }).then(() => {
          hospitalService.setTop(id)
            .then((res) => {
              if (res.data.status === '200') {
                this.$message.success('取消置顶成功！');
              }
              this.getList();
            });
        });
      } else {
        hospitalService.setTop(id)
          .then((res) => {
            if (res.data.status === '200') {
              this.getTopList();
            }
            this.getList();
          });
      }
    },
    deletes(id) {
      this.$confirm('删除此医院？', '提示', {
        type: 'warning',
      }).then(() => {
        hospitalService.deleteHospital(id)
          .then((res) => {
            if (res.data.status === '200') {
              this.$message.success('删除成功！');
            }
            this.getList();
          });
      });
    },
    edit(id) {
      this.$router.push({ name: 'hospital-edit', params: { id } });
    },
    create() {
      this.$router.push({ name: 'hospital-create' });
    },
  },
  created() {
    this.getList();
  },
};
</script>
<style lang="scss">
.el-table .cell {
  line-height: 16px;
}
</style>
<style lang="scss" scoped>
.show {
  background-color: #f0f9eb;
}
.setting-content {
  margin-top: 10px;
}
.title {
  margin-left: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f1f2f7;
  .location {
    font-size: 14px;
    display: none;
    text-decoration: none;
    &>span {
      font-size: 13px;
      font-weight: normal;
    }
  }
  &:hover {
    .location {
      display: inline;
    }
  }
}
.name, .name-en, .small-text {
  line-height: 1.5;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.name {
  font-weight: bold;
  font-weight: 500;
  font-size: 15px;
}
.name-en {
  font-size: 14px;
  line-height: 1.2;
  color: $black-light;
}
.el-row {
  .el-card {
    position: relative;
    margin: 10px 0;
    padding: 0 10px;
    padding-top: 7px;
    margin-bottom: 20px;
    text-align: center;
    .img {
      margin: 0 auto;
      width: auto;
      height: 100px;
    }
    .content {
      &>.el-button {
        padding: 10px 0;
      }
    }
  }
}
.danger {
  color: #f56c6c;
}
.upload {
  margin: 0 auto;
  width: 360px;
}
.content {
  display: inline-block;
}
.small-text {
  font-size: 12px;
  color: $grey-dark;
}
.cursor {
  &:hover {
    color: $blue;
    cursor: pointer;
  }
}
</style>



