<template>
  <div class="setting-section">
    <div class="setting-title">
      <span>友情链接</span>
    </div>
    <div class="setting-content" v-loading="loading">
      <el-table
        :data="list"
        style="width: 100%">
        <el-table-column
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          label="名称">
          <template slot-scope="scope">
            <span
              class="primary"
              type="text"
              @click="view(scope.row.url)">{{ scope.row.name }}</span>
            <span class="small-text" v-if="scope.row.nameEn"><br>{{ scope.row.nameEn }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="url"
          label="地址">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="edit(scope.row.id)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="deletes(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="mt-md">
      <el-button
        type="primary"
        @click="openDialogVisible">新增链接</el-button>
    </div>
    <el-dialog
      title="新增链接"
      :visible.sync="dialogVisible"
      width="500px">
      <el-form label-width="80px" :model="link">
        <el-form-item label="名称">
          <el-input v-model="link.name" placeholder="输入链接名称"></el-input>
        </el-form-item>
        <el-form-item label="英文名称">
          <el-input v-model="link.nameEn" placeholder="输入链接英文名称"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="link.url" placeholder="例：http://www.baidu.com/"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addLink">新 增</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="编辑链接"
      :visible.sync="editDialogVisible"
      width="500px">
      <el-form label-width="80px" :model="oldlink">
        <el-form-item label="名称">
          <el-input v-model="oldlink.name" placeholder="输入链接名称"></el-input>
        </el-form-item>
        <el-form-item label="英文名称">
          <el-input v-model="oldlink.nameEn" placeholder="输入链接英文名称"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="oldlink.url" placeholder="例：http://www.baidu.com/"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editSubmit">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Service from '@/services/others';

export default {
  name: 'links',
  data() {
    return {
      list: [],
      loading: false,
      id: '',
      dialogVisible: false,
      editDialogVisible: false,
      link: {
        name: '',
        nameEn: '',
        url: '',
      },
      oldlink: {
        id: '',
        name: '',
        nameEn: '',
        url: '',
      },
    };
  },
  methods: {
    getLinks() {
      this.loading = true;

      Service.getLinks()
        .then((res) => {
          const data = res.data.data;
          this.list = data;
          this.loading = false;
        }).catch(() => {
          this.loading = false;
        });
    },
    checkUrl(url) {
      if (!url) return false;
      const exp = new RegExp(/http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- .\/?%&=]*)?/);
      return exp.test(url);
    },
    openDialogVisible() {
      this.link = { name: '', url: '', nameEn: '' };
      this.dialogVisible = true;
    },
    addLink() {
      const name = this.link.name;
      const nameEn = this.link.nameEn;
      const url = this.link.url;
      if (!this.checkUrl(url)) {
        this.$message.warning('请输入合法的链接地址！');
        return;
      }
      if (name) {
        this.$message.warning('请输入链接名称和完整的Url');
      }

      Service.addLink(name, nameEn, url)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success('新增成功！');
          }
          this.dialogVisible = false;
          this.getLinks();
        });
    },
    // 查看
    view(url) {
      window.open(url, '_blank');
    },
    // 编辑
    edit(id) {
      this.id = id;

      Service.getLinkById(id)
        .then((res) => {
          if (res.data.status === '200') {
            this.oldlink = res.data.data;
            this.editDialogVisible = true;
          }
        });
    },
    editSubmit() {
      const id = this.id;
      const name = this.oldlink.name;
      const nameEn = this.oldlink.nameEn;
      const url = this.oldlink.url;
      if (!this.checkUrl(url)) {
        this.$message.warning('请输入合法的链接地址！');
        return;
      }
      if (name) {
        this.$message.warning('请输入链接名称和完整的Url');
      }

      Service.editLink(id, name, nameEn, url)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success('编辑成功！');
          }
          this.getLinks();
          this.editDialogVisible = false;
        });
    },
    deletes(id) {
      this.$confirm('确定删除链接？', '提示', {
        type: 'warning',
      }).then(() => {
        Service.deleteLink(id)
          .then((res) => {
            if (res.data.status === '200') {
              this.$message.success('删除成功！');
            }
            this.getLinks();
          });
      });
    },
  },
  created() {
    this.getLinks();
  },
};
</script>
<style lang="scss">
  .el-table .cell {
    line-height: 16px;
  }
</style>
<style lang="scss" scoped>
.el-pagination {
  text-align: center;
}
.el-button+.el-button {
  margin-left: 2px;
}
.flexbox-around {
  display: flex;
  justify-content: center;
}
.small-text {
  color: #999;
  padding-left: 15px;
  line-height: 2;
}
.upload {
  margin: 0 auto;
  width: 360px;
}
.primary {
  cursor: pointer;
}
</style>



