<template>
  <div class="setting-section" v-loading="loading">
    <div class="setting-title">
      <div class="flexbox-between">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ name: 'news' }">新闻中心管理</el-breadcrumb-item>
          <el-breadcrumb-item>发布新闻</el-breadcrumb-item>
        </el-breadcrumb>
        <back-btn>返回</back-btn>
      </div>
    </div>
    <div class="setting-content">
      <el-form ref="news" :model="news" label-width="100px" label-position="left">
        <el-form-item label="新闻标题">
          <el-input v-model="news.title" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="新闻类型">
          <el-radio-group v-model="news.type" size="mini">
            <el-radio-button label="中心动态"></el-radio-button>
            <el-radio-button label="通知公告"></el-radio-button>
            <el-radio-button label="行业资讯"></el-radio-button>
            <el-radio-button label="图片新闻"></el-radio-button>
            <el-radio-button label="技术专题"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="新闻摘要">
          <el-input type="textarea" v-model="news.tabloid" size="mini" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="附件">
          <el-upload
            multiple
            class="upload"
            ref="upload"
            :action="actions"
            :headers="headers"
            :limit="10"
            :on-change="handleChange"
            :file-list="fileList">
            <el-button size="mini" type="primary" v-if="!fileList.length">添加附件</el-button>
            <el-button size="mini" type="primary" v-else>继续添加附件</el-button>
            <span slot="tip" class="small-text ml-md" style="color: red;">* 支持多文件上传！(最多 10个, 最大 100M!)</span>
          </el-upload>
        </el-form-item>
        <el-form-item label="新闻内容">
          <div style="height: 460px;">
            <quill-editor
              v-model="news.content"
              ref="quillEditor"
              style="height: 85%;">
            </quill-editor>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            @click="createNews">立即发布</el-button>
          <el-button
            type="text"
            size="medium"
            @click="preview">预览</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import CONFIG from '@/config';
import axios from 'axios';
import BackBtn from '@/components/common/back';
import NewsServices from '@/services/news';
import storage from '@/services/localStorage';
import { replaceImgSrc, formatImgSrc } from '@/services/utils';

export default {
  name: 'new-create',
  data() {
    return {
      news: {
        title: '',
        type: '',
        tabloid: '',
        content: '',
      },
      // editorOption: {
      //   modules: {
      //     imageDrop: true,
      //     imageResize: {
      //       displayStyles: {
      //         backgroundColor: 'black',
      //         border: 'none',
      //         color: 'white',
      //       },
      //       modules: [ 'Resize', 'DisplaySize', 'Toolbar' ],
      //     },
      //   },
      // },
      fileList: [],
      actions: `${CONFIG.baseUrl}attachment/asyncUploadFile`,
      loading: false,
    };
  },
  computed: {
    headers() {
      return { Authorization: axios.defaults.headers.common.Authorization };
    },
  },
  methods: {
    createNews() {
      const news = this.news;
      const title = news.title;
      const type = news.type;
      const tabloid = news.tabloid;
      const _content = news.content;
      const attachments = this.getFileList(this.fileList);
      if (!title) {
        this.$message.warning('请填写新闻标题！(1 - 50字)');
        return;
      }
      if (!type) {
        this.$message.warning('请选择新闻的类型！');
        return;
      }
      if (!_content) {
        this.$message.warning('请输入新闻的内容！内容不可为空！');
        return;
      }
      this.loading = true;
      formatImgSrc(_content).then(axios.spread((...args) => {
        const arr = [];
        args.map((val) => {
          arr.push(val.data.data.url);
        });
        const content = `<div class="ql-editor">${replaceImgSrc(_content, arr)}</div>`;

        NewsServices.create(title, type, tabloid, content, attachments)
          .then((res) => {
            this.loading = false;
            this.$message.success('新闻成功发布！');
            this.$router.back();
          }).catch(() => {
            this.loading = false;
          });
      })).catch(() => {
        this.loading = false;
      });
    },
    getType(type) {
      switch (type) {
        case '中心动态':
          return 'news';
        case '通知公告':
          return 'notice';
        case '行业资讯':
          return 'infos';
        case '图片新闻':
          return 'pic-news';
        case '技术专题':
          return 'subject';
        default:
          return '';
      };
    },
    // 查看预览新闻
    view(type, id) {
      // const baseUrl = 'http://localhost:8011';
      const baseUrl = 'http://htcc.org.cn';
      let lang = '';
      if (storage.lang === 'en') lang = '/en';
      if (type === '技术专题') {
        window.open(`${baseUrl + lang}/page/research/${this.getType(type)}-article/?id=${id}`, '_blank');
      } else {
        window.open(`${baseUrl + lang}/page/news/${this.getType(type)}-article/?id=${id}`, '_blank');
      }
    },
    // 预览
    preview() {
      const news = this.news;
      const id = 100010;
      const title = news.title;
      const type = news.type;
      const tabloid = news.tabloid;
      const _content = news.content;
      const attachments = this.getFileList(this.fileList);
      if (!title) {
        this.$message.warning('请填写新闻标题！(1 - 50字)');
        return;
      }
      if (!type) {
        this.$message.warning('请选择新闻的类型！');
        return;
      }
      if (!_content) {
        this.$message.warning('请输入新闻的内容！内容不可为空！');
        return;
      }
      this.loading = true;
      formatImgSrc(_content).then(axios.spread((...args) => {
        const arr = [];
        args.map((val) => {
          arr.push(val.data.data.url);
        });
        const content = `<div class="ql-editor">${replaceImgSrc(_content, arr)}</div>`;
        
        NewsServices.edit(id, title, type, tabloid, content, attachments)
          .then((res) => {
            this.loading = false;
            this.view(type, id);
          }).catch(() => {
            this.loading = false;
          });
      })).catch(() => {
        this.$message.success('预览失败，请稍后尝试！');
        this.loading = false;
      });
    },
    // 控制文件列表
    handleChange(file, fileList) {
      this.fileList = fileList.slice(-10);
    },
    getFileList(files) {
      const list = [];
      files.map((val) => {
        list.push(val.response.data[0]);
      });
      return list;
    },
  },
  created() {},
  components: { BackBtn },
};
</script>
<style lang="scss" scoped>
.el-form {
  .el-form-item {
    margin-top: 20px;
  }
}
</style>



