<template>
  <div class="setting-section">
    <div class="setting-title inline-center">
      <span>数 据 统 计</span>
    </div>
    <div class="setting-content">
      <div class="cards">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>今日访问量</span>
          </div>
          <div class="text item">
            <h1 style="color: #22c36a;">{{ dayNumber }}</h1>
          </div>
        </el-card>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>本月访问量</span>
          </div>
          <div class="text item">
            <h1 style="color: #22c36a;">{{ monthNumber }}</h1>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>
<script>
import DataService from '@/services/data';

export default {
  name: 'dashboard',
  data() {
    return {
      dayNumber: 0,
      monthNumber: 0,
      loading: false,
    };
  },
  methods: {
    getData() {
      DataService.getDataByDay()
        .then((res) => {
          if (res.data.status === '200') {
            this.dayNumber = res.data.data || 0;
          }
        });
      DataService.getDataByMonth()
        .then((res) => {
          if (res.data.status === '200') {
            this.monthNumber = res.data.data || 0;
          }
        });
    },
  },
  created() {
    this.getData();
  },
}
</script>
<style lang="scss" scoped>
.setting-section {
  height: 100%;
}
.setting-content {
  position: relative;
  height: 100%;
}
.cards {
  top: 0;
  margin-top: 10%;
  width: 100%;
  text-align: center;
}
.box-card {
  margin: 0 20px;
  width: 280px;
  display: inline-block;
  .item {
    padding: 30px 0;
    h1 {
      text-align: center;
    }
  }
}
</style>



