<template>
  <div>
    <span class="small-text">快速选择：</span>
    <el-select v-model="city" filterable placeholder="快速选择城市" size="mini" @change="cityChange">
      <el-option
        v-for="item in list"
        :key="item.address_id"
        :label="item.address_name"
        :value="item.address_id">
        <span>{{ item.address_name }}</span>
        <span class="small-text">{{ item.address_name_en }}</span>
      </el-option>
    </el-select>
  </div>
</template>
<script>
import Services from '@/services/others';

export default {
  name: 'fast-select',
  props: ['id'],
  data() {
    return {
      list: [],
      city: '',
    };
  },
  methods: {
    cityChange(val) {
      let option = '';
      this.list.forEach(item => {
        if (item.address_id === val) {
          option = item;
        }
      });
      this.$emit('fastSelect', option);
    },
  },
  mounted() {
    Services.getAddress()
      .then((res) => {
        if (res.data.status === '200') {
          this.list = res.data.data;
          this.city = this.id || this.list[0].address_id;
          this.cityChange(this.city);
        }
      });
  },
};
</script>
<style lang="scss" scoped>
  .small-text {
    color: $grey-dark;
  }
</style>



