import Axios from 'axios';
import ImageServices from '@/services/others';
const prefix = 'http://htcc.org.cn';

const imgReg = /<img\ssrc=[\'\"]data:image.*?(?:>|\/>)/gi;
const srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;

// 创建请求组
const generateRequest = (params, call) => {
  const len = params.length;
  const arr = [];
  for (let i = 0; i < len; i += 1) {
    arr.push(call(params[i]));
  }
  return arr;
};

const setImgPrefix = (content) => {
  return content.replace(/<img\ssrc=[\'\"]/gi, `<img src="${prefix}`);
};

// 获取 图片Src 数组
const getSrcArr = (content) => {
  const arr = content.match(imgReg) || [];
  const len = arr.length;
  const rsArr = [];
  
  for (let i = 0; i < len; i += 1) {
    rsArr.push(arr[i].match(srcReg)[1]);
  }
  return rsArr;
};

const replaceImgSrc = (content, arr) => {
  content = content.replace(/http:\/\/htcc\.org\.cn/gi, '');
  content = content.replace(/<img\ssrc=[\'\"]data:image.*?(?:>|\/>)/gi, '|');
  const _arr = content.split('|');
  const len = arr.length;
  let _content = _arr[0];
  for (let i = 0; i < len; i += 1) {
    _content += `<img src="${arr[i]}">${_arr[i + 1]}`;
  }
  return _content;
};

// 发送请求组
const formatImgSrc = (content) => {
  const arr = getSrcArr(content);
  return Axios.all(generateRequest(arr, ImageServices.getImgAddress));
};

export {
  formatImgSrc,
  replaceImgSrc,
  setImgPrefix,
};



