<template>
  <div class="page">
    <div class="login-box">
      <div class="login-container" :class="{ shake: loginFail }">
        <div class="header">
          <div></div>
          <h2 class="platname">国家远程医疗中心</h2>
        </div>
        <div class="content login-form" autocomplete="off">
          <div class="form-group">
            <span>用户名</span>
            <el-input
              ref="user"
              size="mini"
              @keyup.enter.native="login"
              v-model="user.username"
              autofocus
              placeholder="请输入用户名"></el-input>
          </div>
          <div class="form-group">
            <span>密码</span>
            <el-input
              type="password"
              ref="pwd"
              size="mini"
              @keyup.enter.native="login"
              placeholder="请输入密码"
              v-model="user.password"></el-input>
          </div>
          <el-button
            v-if="!isLoging"
            :disabled="isLoging"
            class="login-btn"
            type="primary"
            @click="login">登录</el-button>
          <el-button
            v-else
            icon="el-icon-loading"
            class="login-btn"
            type="primary"
            disabled>正在登录...</el-button>
        </div>
      </div>
    </div>
    <footer><span>NTC | 国家远程医疗中心 - 管理系统</span></footer>
  </div>
</template>

<script>
import UserService from '@/services/login';

export default {
  name: 'login',
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
      loginFail: false,
      refresh: '',
      isLoging: false,
    };
  },
  methods: {
    // 动画
    shake() {
      this.loginFail = true;
      setTimeout(() => {
        this.loginFail = false;
      }, 400);
    },
    // 空表单自动聚焦
    toFocus() {
      if (!this.user.username) {
        this.$refs.user.focus();
      } else if (this.user.password === '') {
        this.$refs.pwd.focus();
      }
    },
    login() {
      const username = this.user.username;
      const password = this.user.password;
      if (!username || !password) {
        this.toFocus();
        return;
      }
      this.isLoging = true;
      UserService.login(username, password)
        .then(() => {
          this.isLoging = false;
          this.$notify({
            title: '登录成功',
            message: '欢迎使用，国远官网管理系统',
            type: 'success',
          });
          this.$router.push('/');
        }).catch((res) => {
          this.shake();
          this.isLoging = false;
          this.$notify({
            title: '登录失败',
            message: `${res.data.msg}`,
            type: 'error',
          });
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.page {
  height: 100%;
  background: url("/static/images/bg.png") no-repeat;
  background-color: $white;
  background-size: cover;
  background-position: center;
}
.login-box {
  position: absolute;
  top: calc(50% - 60px);
  left: 50%;
  width: 320px;
  transform: translateY(-50%) translateX(-50%);
  // 3D
  perspective: 200px;
}
.login-container {
  position: relative;
  padding: 30px 25px;
  font-size: 14px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 15px 30px 0px rgba(0, 0, 1, 0.3);
  .header {
    position: relative;
    text-align: center;
    &:after {
      content: "";
      width: 100%;
      height: 1px;
      background: linear-gradient(
        to right,
        $white 0,
        #e4e7ed 30px,
        #e4e7ed calc(100% - 30px),
        $white 100%
      );
      display: block;
    }
  }
  .platname {
    padding-bottom: 15px;
    font-size: 18px;
    line-height: 1.7;
    color: $black-light;
    text-align: center;
    font-weight: normal;
    letter-spacing: 0.1rem;
  }
  .content {
    margin: 0 25px;
  }
}
.login-form {
  .form-group {
    margin: 14px 0;
    .getcode-btn {
      margin-left: 15px;
      margin-right: -50px;
    }
    &>span {
      color: $grey-dark;
      font-size: 12px;
      font-weight: 400;
      line-height: 24px;
      user-select: none;
      display: block;
    }
  }
  .login-btn {
    margin-top: 10px;
    width: 100%;
    &-text {
      margin-left: 1rem;
      letter-spacing: 1em;
    }
  }
}
footer {
  position: absolute;
  width: 100%;
  bottom: 0px;
  & > span {
    margin: 0 auto;
    width: 100%;
    height: 2.7rem;
    color: $black-dark;
    text-align: center;
    letter-spacing: 0.4rem;
    display: block;
    user-select: none;
  }
}
.shake {
  animation-duration: 400ms;
  animation-timing-function: ease-in-out;
  animation-name: panelShakeAnimation;
}

@keyframes panelShakeAnimation {
  12.5% {
    transform: translateX(-6px) rotateY(-5deg);
  }
  37.5% {
    transform: translateX(5px) rotateY(4deg);
  }
  62.5% {
    transform: translateX(-3px) rotateY(-2deg);
  }
  87.5% {
    transform: translateX(2px) rotateY(1deg);
  }
  100% {
    transform: translateX(0);
  }
}
</style>



