<template>
  <header class="header" style="height: 50px;">
    <div class="tenant">
      <span class="primary">国家远程医疗中心</span>
    </div>
    <div class="date">
      <span class="time">{{ time }}</span>
      <span class="weekday">{{ weekday }}</span>
    </div>
    <div class="userinfo">
      <el-button
        type="text"
        size="lg"
        class="p-none mr-md"
        v-if="language === 'en'"
        @click="cLang('cn')">EN</el-button>
      <el-button
        type="text"
        class="p-none mr-md"
        size="lg"
        v-else
        @click="cLang('en')">CN</el-button>
      <!-- 用户信息面板 -->
      <span class="welcome" v-popover:info>您好，<strong>{{ username }}</strong>
        <!-- <i class="el-icon-arrow-down"></i> -->
      </span>
      <span class="logout" @click="logout">
        <i class="el-icon-evm-exit small-text fb"
          title="登出"></i>
        <span class="small-text fb">注销</span>
      </span>
    </div>
  </header>
</template>
<script>
import UserService from '@/services/login';
import storage from '@/services/localStorage';

const weekdays = ['星期天', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];

export default {
  name: 'header-bar',
  data() {
    return {
      time: '',
      weekday: '',
      minute: '',
      username: '',
      language: '',
    };
  },
  methods: {
    // 获取用户信息
    getUser() {
      UserService.getUser()
        .then((res) => {
          const user = res.data.data;
          this.username = user.username;
          window.USER_NAME = user.username;
        });
    },
    // 登出
    logout() {
      this.$confirm('立即注销用户？', '退出', {
        confirmButtonText: '注销',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        setTimeout(() => UserService.logout(), 300);
        this.$notify.success({
          title: '成功',
          message: '注销成功',
        });
      });
    },
    // 切换语言
    cLang(lang) {
      let text = '中文版';
      if (lang === 'en') {
        text = '英文版';
      }
      this.$confirm(`切换至${text}管理页面？`, '提示', {
        type: 'warning',
      }).then(() => {
        storage.lang = lang;
        window.location.reload();
      });
    },
    // 时间显示定制化
    getTime() {
      const date = new Date();
      this.time = date.toTimeString().split(' ')[0].substring(0, 5);
      this.weekday = weekdays[date.getDay()];
    },
  },
  watch: {
    // 时间变更时重新获取时间
    minute() {
      this.getTime();
    },
  },
  created() {
    this.language = storage.lang || 'cn';
    this.minute = new Date().getMinutes();
    // 间隔 1s 获取
    setInterval(() => {
      this.minute = new Date().getMinutes();
    }, 1000);
    this.getUser();
  },
};
</script>
<style lang="scss" scoped>
.header {
  position: relative;
  padding: 0 25px 0 25px;
  width: 100%;
  height: 100%;
  box-shadow: 0 -1px 10px #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $white;
  .tenant {
    strong {
      font-size: 15px;
      color: $blue;
      // font-size: 18px;
      cursor: pointer;
      user-select: none;
    }
  }
  .date {
    user-select: none;
    .time {
      letter-spacing: 1px;
      font-size: 16px;
      font-weight: 500;
    }
    .weekday {
      font-size: 12px;
    }
  }
  .userinfo {
    user-select: none;
    .welcome {
      padding: 5px;
      user-select: none;
      cursor: pointer;
      strong {
        font-weight: 500;
      }
      margin-right: 12px;
    }
    .logout {
      position: relative;
      padding: 2px 3px;
      border-radius: 100%;
      color: $red;
      border: 1px solid transparent;
      cursor: pointer;
      &:before {
        position: absolute;
        left: -7px;
        content: '';
        width: 1px;
        height: 20px;
        background: linear-gradient(to bottom,
                    $white, $grey-light 10px,
                    $grey-light calc(100% - 10px), $white);
        display: inline-block;
      }
    }
  }
}
.panel {
  max-height: 330px;
  overflow: auto;
  .warnings {
    position: relative;
    margin-left: -17px;
    padding-right: 10px;
    width: 235px;
    font-size: 12px;
    p {
      line-height: 1.3rem;
    }
    .warn-hd {
      margin-top: -5px;
      display: flex;
      justify-content: space-between;
      flex-wrap: nowrap;
      &>span:first-of-type {
        font-weight: bold;
      }
    }
    .warn-content {
      cursor: pointer;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      word-break: break-all;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    .warn-content.normal {
      display: block;
    }
    .close-btn {
      position: absolute;
      top: -4px;
      right: -8px;
      font-size: 12px;
      padding: 1px 1px 4px 4px;
      margin: 1px 1px 0 0;
      border-radius: 0 5px 0 20px;
      cursor: pointer;
    }
  }
  .panel-content {
    .user-pic {
      margin: 0 auto;
      width: 60px;
      height: 60px;
      border-radius: 100%;
      border: 3px solid $white;
      box-shadow: -2px -2px 4px #f1f2f7,
                  2px 2px 4px #f1f2f7;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .hr {
      border-bottom: 1px dashed #f1f2f7;
    }
    .user-content {
      padding: 7px 0;
      color: #666;
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      &>span {
        line-height: 1.5;
        vertical-align: sub;
        display: inline-block;
      }
    }
    div {
      font-size: 12px;
      color: #999;
      line-height: 2rem;
      text-align: center;
    }
  }
  .panel-footer {
    display: flex;
    justify-content: center;
    button {
      color: $blue;
      width: 100%;
      &:hover {
        background-color: #fbfbfb;
      }
      &:last-of-type {
        color: $red;
      }
    }
  }
}
.el-alert.el-alert--error {
  margin-bottom: 3px;
}
</style>



