<template>
  <nav class="nav">
    <div class="product-title">
      <span class="fb">管理系统</span>
    </div>
    <div class="menu-group">
      <template v-for="menuGroup in menus">
        <h6 class="title" :key="menuGroup.code">{{ menuGroup.name }}</h6>
        <router-link
          class="menu-item"
          tag="div"
          v-for="item in menuGroup.children"
          :key="item.code"
          :to="item.url">
          <i :class="item.icon"></i>
          {{ item.name }}
        </router-link>
      </template>
    </div>
  </nav>
</template>
<script>
export default {
  name: 'nav-main',
  data() {
    return {
      // 默认菜单数据
      model: [
        {
          id: 0,
          level: 1,
          code: 'DASHBOARD',
          name: '看板',
          sort: 0,
        },
        {
          id: '01',
          level: 2,
          code: 'DATA_DASHBOARD',
          pcode: 'DASHBOARD',
          name: '数据看板',
          sort: 1,
          url: '/dashboard',
        },
        {
          id: 1,
          level: 1,
          code: 'CONTENTS',
          name: '内容管理',
          sort: 1,
        },
        {
          id: 11,
          level: 2,
          icon: 'el-icon-tickets',
          code: 'STATIC_MANAGER',
          pcode: 'CONTENTS',
          name: '静态内容',
          sort: 1,
          url: '/static',
        },
        {
          id: 12,
          level: 2,
          icon: 'el-icon-tickets',
          code: 'NEWS_MANAGER',
          pcode: 'CONTENTS',
          name: '新闻中心',
          sort: 2,
          url: '/news',
        },
        {
          id: 13,
          level: 2,
          icon: 'el-icon-tickets',
          code: 'LINKS_MANAGER',
          pcode: 'CONTENTS',
          name: '友情链接',
          sort: 3,
          url: '/links',
        },
        {
          id: 14,
          level: 2,
          icon: 'el-icon-tickets',
          code: 'HOSPITAL_MANAGER',
          pcode: 'CONTENTS',
          name: '协同医院',
          sort: 4,
          url: '/hospital',
        },
        {
          id: 2,
          level: 1,
          code: 'SOURCE',
          name: '资源管理',
          sort: 2,
        },
        {
          id: 22,
          level: 2,
          icon: 'el-icon-picture-outline',
          code: 'IMAGES',
          pcode: 'SOURCE',
          name: '静态图片自定义',
          sort: 1,
          url: '/customization',
        },
      ],
      menus: '',
    };
  },
  methods: {
    // 格式化数据
    format() {
      const menulist = this.model;
      const list = {};
      menulist.forEach((item) => {
        if (item.level === 1) {
          if (!list[item.code]) {
            list[item.code] = { ...item, children: [] };
          }
        } else if (item.level === 2) {
          if (list[item.pcode]) {
            list[item.pcode].children.push(item);
          } else {
            list[item.pcode] = { children: [] };
          }
        }
      });
      this.menus = list;
    },
    getMenus() {
      this.format();
    },
  },
  created() {
    this.getMenus();
  },
};
</script>

<style lang="scss" scoped>
.nav {
  height: 100%;
  border: none;
  color: rgba(255, 255, 255, .7);
  background-color: #1f2126;
  overflow: hidden;
  .product-title {
    padding-top: 7px;
    padding: 15.5px 0;
    font-size: 16px;
    color: $white;
    text-align: center;
    border-bottom: 1px solid #666;
    background-color: #1f2126;
  }
  .menu-group {
    padding-top: 10px;
    padding-bottom: 20px;
    height: calc(100% - 50px);
    overflow-y: auto;
  }
  .title, .menu-item {
    padding-left: 39px;
    user-select: none;
  }
  h6.title {
    margin-top: 12px;
    margin-bottom: 3px;
    font-size: 12px;
    font-weight: 400;
    color: #777;
  }
  .menu-item {
    width: 100%;
    line-height: 2.2rem;
    cursor: pointer;
    &>i {
      margin-right: 3px;
    }
    &:hover {
      color: $white;
      transition: .3s color;
    }
    &:after,&:before {
      margin-right: .35rem;
    }
    &.router-link-active {
      color: #2d8cf0;
      border-right: 2px solid #2d8cf0;
      background: linear-gradient(to left, #282d38, #1f2126);
      transition: all .2s ease-in-out;
    }
  }
}
</style>



