<template>
  <div class="setting-section">
    <div class="setting-title">
      <div class="flexbox-between">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ name: 'hospital' }">协同医院</el-breadcrumb-item>
          <el-breadcrumb-item>新增</el-breadcrumb-item>
        </el-breadcrumb>
        <back-btn>返回</back-btn>
      </div>
    </div>
    <div class="setting-content">
      <el-form ref="news" :model="hospital" label-width="120px" label-position="right">
        <el-form-item label="医院图标">
          <div v-if="isDefualt" class="imgDiv">
            <img :src="baseUrl + hospital.image" alt="">
            <el-button type="text" size="small" @click="cancelDefaultImage">取消</el-button>
          </div>
          <el-upload
            v-else
            class="upload"
            ref="upload"
            :action="actions"
            :headers="headers"
            :on-success="onSuccess"
            :data="data"
            name="image"
            list-type="picture-card"
            :on-remove="handleRemove">
            <i class="el-icon-plus"></i>
            <p slot="tip" class="small-text" style="color: red;">
              <span>* 图片尺寸 148*127</span>
              <el-button type="text" size="small" @click="defaultImage" v-if="!isDefualt" class="pl-xs">默认图标</el-button>
            </p>
          </el-upload>
        </el-form-item>
        <el-form-item label="医院名称">
          <el-input type="text" v-model="hospital.name" size="mini" placeholder="郑州大学第一附属医院"></el-input>
        </el-form-item>
        <el-form-item label="医院英文名称">
          <el-input type="text" v-model="hospital.nameEn" size="mini" placeholder="The First Affiliated Hospital of Zhengzhou University"></el-input>
        </el-form-item>
        <el-form-item label="首页地址">
          <el-input type="text" v-model="hospital.url" size="mini" placeholder="http://www.htcc.org.cn/"></el-input>
        </el-form-item>
        <el-form-item label="所属城市">
          <fast-select @fastSelect="getSelected"></fast-select>
          <div class="input-col-2">
            <el-input type="text" v-model="hospital.addressName" size="mini" placeholder="输入中文名称"></el-input>
            <el-input type="text" v-model="hospital.addressNameEn" size="mini" placeholder="Enter English Name"></el-input>
          </div>
          <span class="tips">* 快速选择中，城市对应的英文若有错误，可以手动更改后提交以修正记录！</span>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            @click="create">新增</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import CONFIG from '@/config';
import axios from 'axios';
import BackBtn from '@/components/common/back';
import hospitalService from '@/services/hospital';
import FastSelect from './fast-select.vue';
const DEFAULT_IMAGE = '/u/cms/www/logo/cd169571-719f-4547-a99c-b45550d64978.png';

export default {
  name: 'hospital-create',
  data() {
    return {
      data: {
        path: 'logos',
      },
      hospital: {
        name: '',
        nameEn: '',
        image: '',
        url: '',
        addressName: '',
        addressNameEn: '',
      },
      baseUrl: 'http://www.htcc.org.cn',
      actions: `${CONFIG.baseUrl}news/uploadBanner`,
      isDefualt: false,
    };
  },
  computed: {
    headers() {
      return { Authorization: axios.defaults.headers.common.Authorization };
    },
  },
  methods: {
    onSuccess(res) {
      this.hospital.image = res.data;
      this.$refs.upload.$el.children[1].style.display = 'none';
    },
    handleRemove() {
      setTimeout(() => {
        this.$refs.upload.$el.children[1].style.display = 'inline-block';
      }, 500);
    },
    getSelected(option) {
      this.hospital.addressName = option.address_name;
      this.hospital.addressNameEn = option.address_name_en;
    },
    create() {
      const name = this.hospital.name;
      const nameEn = this.hospital.nameEn;
      const image = this.hospital.image;
      const url = this.hospital.url;
      const addressName = this.hospital.addressName;
      const addressNameEn = this.hospital.addressNameEn;
      if (!name) {
        this.$message.warning('请输入医院名称！');
        return;
      }
      if (!addressName) {
        this.$message.warning('请输入所属城市！');
        return;
      }

      hospitalService.createHospital(name, nameEn,image, url, addressName, addressNameEn)
        .then((res) => {
          if (res.data.status === '200') {
            this.$message.success('新增成功！');
          }
          this.$router.back();
        });
    },
    // 更新图片
    defaultImage() {
      this.hospital.image = DEFAULT_IMAGE;
      this.isDefualt = true;
    },
    // 取消
    cancelDefaultImage() {
      this.hospital.image = '';
      this.isDefualt = false;
    },
  },
  created() {},
  components: { BackBtn, FastSelect },
};
</script>
<style lang="scss" scoped>
.imgDiv {
  &>img {
    margin-bottom: -10px;
  }
}
.tips {
  font-size: 12px;
  color: #f56c6c;
}
.input-col-2 {
  display: flex;
  justify-content: space-between;
  .el-input {
    flex: 1;
    &:last-of-type {
      margin-left: 5px;
    }
  }
}
</style>



