<template>
  <div class="setting-section">
    <div class="setting-title">
      <span>静态内容</span>
    </div>
    <div class="setting-content" v-loading="loading">
      <el-table
        :data="list"
        style="width: 100%">
        <el-table-column
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          label="标题">
          <template slot-scope="scope">
            <span
              class="primary"
              type="text"
              @click="view(scope.row.url, scope.row.id)">{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          label="类型"
          width="85">
        </el-table-column>
        <el-table-column
          prop="update_date"
          label="最后修改时间"
          sortable
          width="150">
        </el-table-column>
        <el-table-column
          prop="page_view"
          label="浏览量"
          sortable
          width="80">
        </el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="edit(scope.row.id)">编辑</el-button>
            <!-- <el-button
              size="mini"
              type="danger"
              @click="remove(scope.row.title, [scope.row.id])">删除</el-button> -->
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-dialog
        title="上传轮播图片（Banner）"
        :visible.sync="dialogVisible"
        width="480px">
        <el-upload
          drag
          ref="upload"
          action="string"
          :auto-upload="false"
          :http-request="uploadFunc"
          class="upload">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">
            <span style="color: red; line-height: 1.5;">* 仅图片 (比例 2:1，且分辨率不小于 1920*960), 大小不超过 10M！</span>
            <span style="color: red; line-height: 1.5;">* 上传封面后，首页将自动轮播此新闻 *</span>
          </div>
        </el-upload>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="upload">上 传</el-button>
        </span>
      </el-dialog> -->
    </div>
  </div>
</template>
<script>
import Service from '@/services/others';

export default {
  name: 'static',
  data() {
    return {
      list: [],
      loading: false,
      dialogVisible: false,
      id: '',
    };
  },
  methods: {
    getNewsData() {
      this.loading = true;

      Service.getList()
        .then((res) => {
          const data = res.data.data;
          this.list = data;
          this.loading = false;
        }).catch(() => {
          this.loading = false;
        });
    },
    // 查看
    view(url, id) {
      window.open(`http://htcc.org.cn${url}`, '_blank');
    },
    // 编辑
    edit(id) {
      this.$router.push({ name: 'static-edit', params: { id } });
    },
  },
  created() {
    this.getNewsData();
  },
};
</script>
<style lang="scss" scoped>
.el-pagination {
  text-align: center;
}
.el-button+.el-button {
  margin-left: 2px;
}
.flexbox-around {
  display: flex;
  justify-content: center;
}
.small-text {
  color: #999;
  padding-left: 15px;
  line-height: 2;
}
.upload {
  margin: 0 auto;
  width: 360px;
}
.primary {
  cursor: pointer;
}
</style>



